  section.checkout-page-main {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
    padding-right: 10px;
    padding-left: 10px;
    background: red;
    flex-direction: column-reverse;

    .checkout-container {
      width: 100%;
      height: calc(100vh - -90px);
    }
    .left-section {
      width: 95%;
    }
    h2.checkout-heading {
      font-size: 19px;
      margin: 10px;
    }
    .contact-info button,
    .checkout-form button,
    .links-section button {
      margin: 10px;
    }
    .email-checkbox {
      position: relative;
      left: 80px;
    }
    .user-email {
      right: 5rem;
    }
    p.subscribed-text {
      margin-left: 10px;
    }
    label.subscribed {
      right: 5rem;
    }
    .checkout-container-2 {
      width: 100%;
      height: 60vh;
    }
    .right-section {
      width: 100%;
    }
    span {
      font-size: 13px;
      font-weight: 700;
      display: flex;
    }
    input.email-input-field {
      left: -78px;
    }
    .button {
      font-size: 12px;
      padding: 6px 12px;
    }
    button.button.continue-btn {
      position: relative;
      left: 5px;
    }
    .Header_userContainer .Header_subMenu {
      position: absolute;
      top: 69%;
